@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";

.com-footer {
  color: white;
  background-color: black;
  font-weight: 600;
  font-family: "Montserrat", sans-serif;
  font-size: 0.8rem;
  padding: $pdMobile;
  @include desktop2k {
    font-size: 1.15rem;
  }
  & > img {
    margin-bottom: 2rem;
    max-width: 40vw;
    @include desktop2k {
      width: 350px;
    }
  }
  .block-footer {
    display: flex;

    ul {
      flex: 1;
      li {
        height: 40px;
        max-width: 70%;
        @include desktop2k {
          height: 70px;
        }
        img {
          margin-right: 20px;
          cursor: pointer;
          @include desktop2k {
            margin-right: 40px;
            width: 50px;
          }
        }
      }
      &.ul-one,
      &.ul-four {
        display: none;
      }
      // .special {
      //   margin-top: 20px;
      // }
    }
  }
  .block-footer-link {
    margin-bottom: 1.5rem;
    img {
      margin-top: 1.5rem;
      margin-right: 1rem;
    }
  }
}

@include desktop {
  .com-footer {
    padding: $pdDesk 50px;
    // margin-left: 5vw;
    & > img {
      margin-bottom: unset;
      max-width: unset;
      position: absolute;
    }
    .spec-li {
      max-width: 250px;
    }
    .block-footer {
      ul {
        &.ul-one,
        &.ul-four {
          display: block;
        }
        .special {
          margin-top: unset;
        }
      }
    }
  }
  .block-footer-link,
  .block-footer-final {
    display: none;
  }
}
